import React from "react";
import { Input } from "@chakra-ui/react";
import {
  FormControl,
  FormLabel,
  FormErrorMessage
} from "@chakra-ui/form-control";
import { Field } from "formik";

const NameFields = () => {
  return (
    <>
      <Field name="name">
        {({ field, form }) => (
          <FormControl isInvalid={form.errors.name && form.touched.name}>
            <FormLabel htmlFor="name"></FormLabel>
            <Input {...field} id="name" placeholder="" />
            <FormErrorMessage>{form.errors.name}</FormErrorMessage>
          </FormControl>
        )}
      </Field>
      <Field name="nameFurigana">
        {({ field, form }) => (
          <FormControl
            isInvalid={form.errors.nameFurigana && form.touched.nameFurigana}
          >
            <FormLabel htmlFor="nameFurigana"></FormLabel>
            <Input {...field} id="nameFurigana" placeholder="" />
            <FormErrorMessage>{form.errors.nameFurigana}</FormErrorMessage>
          </FormControl>
        )}
      </Field>
    </>
  );
};

export default NameFields;
