import React from "react";
import { Input, RadioGroup, Radio, Grid, GridItem } from "@chakra-ui/react";
import {
  FormControl,
  FormLabel,
  FormErrorMessage
} from "@chakra-ui/form-control";
import { Field } from "formik";

const WorkFields = () => {
  return (
    <>
      <Field name="placeOfWork">
        {({ field, form }) => (
          <FormControl
            isInvalid={form.errors.placeOfWork && form.touched.placeOfWork}
          >
            <FormLabel htmlFor="placeOfWork"></FormLabel>
            <Input {...field} id="placeOfWork" placeholder="" />
            <FormErrorMessage>{form.errors.placeOfWork}</FormErrorMessage>
          </FormControl>
        )}
      </Field>
      <Field name="industry">
        {({ field, form }) => (
          <FormControl
            isInvalid={form.errors.industry && form.touched.industry}
          >
            <FormLabel htmlFor="industry"></FormLabel>
            <RadioGroup
              onChange={value => form.setFieldValue(field.name, value)}
              value={field.value}
            >
              <Grid
                templateRows="repeat(3, 1fr)"
                templateColumns="repeat(3, 1fr)"
              >
                <GridItem rowSpan={1} colSpan={1}>
                  <Radio value=""></Radio>
                </GridItem>
                <GridItem rowSpan={1} colSpan={1}>
                  <Radio value=""></Radio>
                </GridItem>
                <GridItem rowSpan={1} colSpan={1}>
                  <Radio value=""></Radio>
                </GridItem>
                <GridItem rowSpan={1} colSpan={1}>
                  <Radio value=""></Radio>
                </GridItem>
                <GridItem rowSpan={1} colSpan={1}>
                  <Radio value=""></Radio>
                </GridItem>
                <GridItem rowSpan={1} colSpan={1}>
                  <Radio value=""></Radio>
                </GridItem>
                <GridItem rowSpan={1} colSpan={1}>
                  <Radio value=""></Radio>
                </GridItem>
                <GridItem rowSpan={1} colSpan={1}>
                  <Radio value=""></Radio>
                </GridItem>
                <GridItem rowSpan={1} colSpan={1}>
                  <Radio value=""></Radio>
                </GridItem>
              </Grid>
            </RadioGroup>
            <FormErrorMessage>{form.errors.industry}</FormErrorMessage>
          </FormControl>
        )}
      </Field>
    </>
  );
};

export default WorkFields;
