import React from "react";
import {
  Center,
  Stack,
  Grid,
  GridItem,
  Checkbox,
  CheckboxGroup,
  Input
} from "@chakra-ui/react";
import {
  FormControl,
  FormLabel,
  FormErrorMessage
} from "@chakra-ui/form-control";
import { Formik, Form, Field } from "formik";
import Header from "../components/Header";
import FormNavigationButtons from "../components/FormNavigationButtons";

function Page4({ questionnaireState, onSubmit, handleGoToPreviousPage }) {
  return (
    <>
      <Header />
      <Center m="8">
        <Formik
          initialValues={{
            experiencedAilments: questionnaireState.experiencedAilments,
            ailmentTimePeriod: questionnaireState.ailmentTimePeriod
          }}
          onSubmit={(values, actions) => {
            setTimeout(() => {
              actions.setSubmitting(false);
              onSubmit(values);
            }, 200);
          }}
        >
          {props => (
            <Form onSubmit={props.handleSubmit}>
              <Stack spacing="3" w="500px">
                <Field name="experiencedAilments">
                  {({ field, form }) => {
                    return (
                      <FormControl
                        isInvalid={
                          form.errors.experiencedAilments &&
                          form.touched.experiencedAilments
                        }
                      >
                        <FormLabel htmlFor="experiencedAilments">
                          
                        </FormLabel>
                        <CheckboxGroup
                          onChange={value => {
                            form.setFieldValue("experiencedAilments", value);
                            if (value.length === 0)
                              form.setFieldValue(
                                "ailmentTimePeriod",
                                undefined
                              );
                          }}
                          defaultValue={field.value || []}
                        >
                          <Grid
                            templateRows="repeat(10, 1fr)"
                            templateColumns="repeat(2, 1fr)"
                          >
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value=""></Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value=""></Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value=""></Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value=""></Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value="">
                                
                              </Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value=""></Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value="">
                                
                              </Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value=""></Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value=""></Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value=""></Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value=""></Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value="">
                                
                              </Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value=""></Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value=""></Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value="X">X</Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value="">
                                
                              </Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value="">
                                
                              </Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value=""></Checkbox>
                            </GridItem>
                            <GridItem rowSpan={1} colSpan={1}>
                              <Checkbox value=""></Checkbox>
                            </GridItem>
                          </Grid>
                        </CheckboxGroup>
                        <FormErrorMessage>
                          {form.errors.experiencedAilments}
                        </FormErrorMessage>
                      </FormControl>
                    );
                  }}
                </Field>
                <Field name="ailmentTimePeriod">
                  {({ field, form }) => {
                    const isAilmentTimePeriodVisible =
                      form.getFieldProps("experiencedAilments").value?.length >
                      0;

                    return (
                      isAilmentTimePeriodVisible && (
                        <FormControl
                          isInvalid={
                            form.errors.ailmentTimePeriod &&
                            form.touched.ailmentTimePeriod
                          }
                        >
                          <FormLabel htmlFor="ailmentTimePeriod">
                            
                          </FormLabel>
                          <Input
                            {...field}
                            id="ailmentTimePeriod"
                            placeholder=""
                          />
                          <FormErrorMessage>
                            {form.errors.ailmentTimePeriod}
                          </FormErrorMessage>
                        </FormControl>
                      )
                    );
                  }}
                </Field>
              </Stack>
              <FormNavigationButtons
                onSubmit={onSubmit}
                handleGoToPreviousPage={handleGoToPreviousPage}
                isLoading={props.isSubmitting}
                values={props.values}
              />
            </Form>
          )}
        </Formik>
      </Center>
    </>
  );
}

export default Page4;
